@import '../constants/index.scss';

.download-app {
  overflow: hidden;
  padding-top: 80px;
  color: $color-primary-content;
  background: linear-gradient(180.74deg, $color-primary 0.58%, $color-secondary 99.36%);

  @media only screen and (min-width: ($media-screen-tablet + 1)) {
    padding-top: 37px;
    background: linear-gradient(90deg, $color-primary 0%, $color-secondary 100%);
  }

  &__wrap {
    display: block;

    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      gap: 40px;
      display: flex;
    }
  }

  &__content {
    gap: 60px;
    display: grid;
    grid-template-columns: repeat(1, minmax(0, 1fr));

    @media only screen and (min-width: ($media-screen-mobile + 1)) {
      gap: 82px;
    }

    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      grid-template-columns: repeat(2, minmax(0, 1fr));
    }
  }

  &__item {
    position: relative;

    .relative {
      z-index: 1;
      position: relative;

      @media only screen and (min-width: ($media-screen-tablet + 1)) {
        margin-top: 45px;
      }
    }

    .key-visual {
      top: 90%;
      z-index: 0;
      left: -50%;
      position: absolute;

      @media only screen and (min-width: ($media-screen-mobile + 1)) {
        left: 0;
        top: 95%;
      }

      @media only screen and (min-width: ($media-screen-tablet + 1)) {
        top: -50%;
        left: 100%;
      }

      img {
        width: 652px;
        height: 652px;
        margin: 0 auto;
        max-width: unset;
        max-height: unset;

        @media only screen and (min-width: ($media-screen-mobile + 1)) {
          width: 800px;
          height: 800px;
        }
      }

    }

    .phone {
      z-index: 1;
      width: 240px;
      height: 335px;
      margin: 0 auto;
      position: relative;

      @media only screen and (min-width: ($media-screen-mobile + 1)) {
        height: 340px;
        max-width: 425px;
        margin-left: auto;
        margin-right: auto;
      }

      @media only screen and (min-width: ($media-screen-tablet + 1)) {
        height: 330px;
      }

      img {
        left: 50%;
        width: 140vw;
        max-width: unset;
        position: absolute;
        object-position: top;
        transform: translate(-50%, 0);

        @media only screen and (min-width: ($media-screen-mobile + 1)) {
          width: 530px;
        }

        @media only screen and (min-width: ($media-screen-tablet + 1)) {
          left: 55%;
          width: 560px;
        }
      }
    }
  }

  &__title {
    font-size: 20px;
    font-weight: 700;
    line-height: 28px;
    margin: 0 0 24px 0;
    text-align: center;

    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      font-size: 28px;
      font-weight: 700;
      line-height: 36px;
      text-align: left;
    }
  }

  &__subtitle {
    font-size: 14px;
    font-weight: 400;
    line-height: 22px;
    margin: 0 0 40px 0;
    text-align: center;

    @media only screen and (min-width: ($media-screen-mobile + 1)) {
      max-width: 425px;
      margin-left: auto;
      margin-right: auto;
    }

    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      font-size: 16px;
      text-align: left;
      font-weight: 400;
      max-width: 365px;
      line-height: 24px;
      margin-bottom: 30px;
    }
  }

  &__qr {
    display: none;

    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      display: block;
    }
  }

  &__downloadtext {
    display: none;

    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      display: block;
    }
  }

  .btn-site {
    gap: 20px;
    display: flex;
    justify-content: center;

    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      display: none;
    }
  }
}