@import '../constants/index.scss';

.direction {

  &--right {
    max-width: 370px;

    @media only screen and (min-width: ($media-screen-mobile + 1)) {
      margin-left: auto;
    }


    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      margin-left: unset;
      margin-right: auto;
    }

    &__pay {
      padding-top: 15px;

      @media only screen and (min-width: ($media-screen-tablet + 1)) {
        padding-top: 80px;
      }

      .tab2 {
        &__item {
          padding-right: 20px;
        }
      }
    }
  }
}

.directtion-mobile {
  padding-top: 40px;
  padding-bottom: 40px;

  &__step {
    list-style: none;
    padding: 0;
    margin: 0;

    li {
      height: 76px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      border-bottom: 1px solid $color-grey-200;

      p {
        &:first-child {
          font-size: 14px;
          font-weight: 600;
          line-height: 18px;
          color: $color-primary-text;
          margin-bottom: 4px;
        }

        &:last-child {
          font-size: 14px;
          font-weight: 400;
          line-height: 22px;
          color: $color-secondary-text;
        }
      }
    }
  }

  &__img {
    height: 270px;
    overflow: hidden;
    padding-top: 20px;
    margin-left: -55px;
    margin-right: -55px;
    position: relative;

    img {
      object-position: top;
    }
  }

  &__tab2 {
    display: flex;
    overflow-y: auto;
    margin-left: -10px;
    margin-right: -10px;
    flex-direction: column-reverse;
    max-height: calc(100vh - 418px);
  }

  &__body {

    .react-multiple-carousel__arrow {

      &--right,
      &--left {
        top: 153px;
        width: 32px;
        height: 32px;
        min-width: unset;
        min-height: unset;
        border: 1px solid $color-grey-200;
        background-color: $color-primary-content;

        &:disabled {
          &::before {
            color: $color-grey-300;
          }
        }

        &::before {
          color: $color-grey-600;
          font-size: 16px;
        }
      }
    }
  }

  &__prev,
  &__next {
    display: flex;
    justify-content: center;

    &.active {
      color: $color-grey-600;
    }
  }

  &__prev {
    left: 50px;
  }

  &__next {
    right: 50px;
  }
}