@import '../constants/index.scss';

.case-stadies {
  padding-top: 40px;
  padding-bottom: 40px;

  @media only screen and (min-width: ($media-screen-tablet + 1)) {
    padding-top: 80px;
    padding-bottom: 80px;
  }

  &__title {
    font-size: 20px;
    font-weight: 700;
    line-height: 28px;
    margin: 0 0 40px 0;

    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      font-size: 28px;
      line-height: 36px;
      margin-bottom: 44px;
    }
  }
}