@import '../../constants/index.scss';

.eight-shell {
  padding-top: 40px;
  padding-bottom: 40px;
  background-color: $color-grey-100;

  @media only screen and (min-width: ($media-screen-tablet + 1)) {
    padding-top: 80px;
    padding-bottom: 80px;
  }

  &__title {
    font-size: 20px;
    font-weight: 700;
    line-height: 28px;
    margin: 0 0 10px 0;

    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      font-size: 28px;
      line-height: 36px;
    }
  }

  &__subtitle {
    font-size: 14px;
    font-weight: 400;
    max-width: 715px;
    line-height: 22px;
    text-align: center;
    margin: 0 auto 24px auto;
    color: $color-secondary-text;

    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      font-size: 16px;
      line-height: 24px;
      margin-bottom: 40px;
    }
  }

  &__content {
    gap: 12px;
    display: flex;
    margin: 0 auto;
    flex-wrap: wrap;
    max-width: 960px;
    justify-content: center;

    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      gap: 20px;
    }

    .empty {
      display: none;

      @media only screen and (min-width: ($media-screen-tablet + 1)) {
        display: block;
      }
    }
  }

  &__item {
    text-align: center;
    min-width: calc(33.33% - 12px);
    max-width: calc(33.33% - 12px);

    @media only screen and (min-width: ($media-screen-mobile + 1)) {
      min-width: calc(16.67% - 12px);
      max-width: calc(16.67% - 12px);
    }

    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      min-width: calc(25% - 20px);
      max-width: calc(25% - 20px);
    }

  }

  &__img {
    display: flex;
    height: 52.5px;
    align-items: center;
    justify-content: center;
    border-radius: $corner-c1;
    background-color: $color-primary-content;

    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      height: 114px;
      border-radius: $corner-c6;
    }

    img {
      transform: scale(.7);
    }
  }
}