@import '../constants/index.scss';

.carousel2 {
  margin-left: -6px;
  margin-right: -6px;

  @media only screen and (min-width: ($media-screen-tablet + 1)) {
    margin-left: -10px;
    margin-right: -10px;
  }

  .react-multi-carousel-list {
    overflow: unset;
  }

  &__wrap {
    height: 100%;
    padding: 0 6px;

    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      padding: 0 10px;
    }
  }

  &__spacing {
    height: 100%;
    overflow: hidden;
    border-radius: 6px;
    border: 1px solid $color-grey-200;

    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      border-radius: $corner-c3;
    }
  }

  &__img {
    height: 144px;
    margin-bottom: 16px;

    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      height: 222px;
    }
  }

  &__stream {
    font-size: 10px;
    font-weight: 400;
    line-height: 16px;
    letter-spacing: 0px;


    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      font-size: 12px;
      line-height: 20px;
    }
  }

  &__title {
    font-size: 14px;
    font-weight: 600;
    line-height: 18px;
    margin: 0 0 22px 0;

    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      font-size: 16px;
      line-height: 20px;
      margin-bottom: 14px;
    }
  }

  &__text {
    padding-left: 16px;
    padding-right: 16px;
  }

  img {
    pointer-events: none;
  }

  .react-multiple-carousel__arrow {

    &--right,
    &--left {
      top: calc(-40px - 32px);
      width: 32px;
      height: 32px;
      min-width: unset;
      min-height: unset;
      border: 1px solid $color-grey-200;
      background-color: $color-primary-content;

      &:disabled {
        &::before {
          color: $color-grey-300;
        }
      }

      &::before {
        color: $color-grey-600;
        font-size: 16px;
      }
    }

    &--left {
      left: unset;
      right: calc(16px + 32px + 10px);
    }

    &--right {
      right: 10px;
    }
  }
}