@import '../constants/index.scss';

.spacing {
  height: 68px;

  @media only screen and (min-width: ($media-screen-tablet + 1)) {
    height: 128px;
  }
}

.carousel-brands {
  padding-top: 20px;
  padding-bottom: 20px;

  @media only screen and (min-width: ($media-screen-mobile + 1)) {
    padding-top: 40px;
  }

  @media only screen and (min-width: ($media-screen-tablet + 1)) {
    padding-bottom: 40px;
  }

  &__title {
    margin: 0;
    display: none;

    @media only screen and (min-width: ($media-screen-mobile + 1)) {
      display: block;
      font-size: 20px;
      font-weight: 700;
      line-height: 28px;
      margin-bottom: 20px;
    }

    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      font-size: 44px;
      font-weight: 700;
      line-height: 52px;
      margin-bottom: 40px;
    }
  }
}

.carousel3 {
  margin-left: -6px;
  margin-right: -6px;

  @media only screen and (min-width: ($media-screen-tablet + 1)) {
    margin-left: -10px;
    margin-right: -10px;
  }

  .react-multi-carousel-list {
    overflow: unset;
  }

  &__wrap {
    height: 100%;
    padding: 0 6px;

    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      padding: 0 10px;
    }
  }

  &__img {
    height: 125.25px;
    overflow: hidden;
    border-radius: 6px;

    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      height: 337.5px;
      border-radius: $corner-c6;
    }
  }

  img {
    pointer-events: none;
  }
}