@import '../constants/index.scss';

.footer {
  padding-top: 40px;
  padding-bottom: 40px;

  @media only screen and (min-width: ($media-screen-tablet + 1)) {
    padding-top: 80px;
    padding-bottom: 80px;
  }

  ul {
    margin: 0;
    padding: 0;
    list-style: none;
  }

  &__logo {
    height: 62px;
    width: 172px;
    margin-bottom: 20px;

    svg {
      width: 100%;
      height: 100%;
    }
  }

  &__social {
    margin-bottom: 20px;

    ul {
      gap: 12px;
      display: flex;
      list-style: none;
      align-items: center;

      li {
        width: 20px;
        height: 20px;
        display: flex;
        align-items: center;
        justify-content: center;
      }
    }
  }

  &__content {
    gap: 40px;
    display: flex;
    max-width: 100%;
    flex-direction: column;

    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      gap: 60px;
      display: grid;
      grid-template-columns: repeat(10, minmax(0, 1fr));
    }
  }

  &__item {
    &:first-child {
      order: 2;

      @media only screen and (min-width: ($media-screen-tablet + 1)) {
        order: 1;
        grid-column: span 4/span 4;
      }
    }

    &:last-child {
      order: 1;

      @media only screen and (min-width: ($media-screen-tablet + 1)) {
        order: 2;
        grid-column: span 6/span 6;
      }
    }
  }

  &__title {
    margin: 0;
    font-size: 14px;
    font-weight: 700;
    line-height: 18px;
    margin-bottom: 12px;

    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      font-size: 20px;
      line-height: 28px;
    }
  }

  &__info {
    li {
      gap: 12px;
      display: flex;
      font-size: 12px;
      font-weight: 400;
      line-height: 20px;
      letter-spacing: 0px;
      margin-bottom: 12px;
      color: $color-secondary-text;

      @media only screen and (min-width: ($media-screen-tablet + 1)) {
        font-size: 14px;
        line-height: 22px;
      }

      svg {
        min-width: 20px;
        min-height: 20px;
      }
    }
  }

  &__lisence {
    font-size: 10px;
    font-weight: 400;
    line-height: 16px;
    color: $color-grey-300;

    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      font-size: 12px;
      line-height: 20px;
    }
  }

  &__subcontent {
    gap: 40px;
    display: grid;
    grid-template-columns: repeat(1, minmax(0, 1fr));

    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      gap: 60px;
      grid-template-columns: repeat(3, minmax(0, 1fr));
    }
  }

  &__subitem {
    ul {
      li {
        margin-bottom: 24px;

        @media only screen and (min-width: ($media-screen-tablet + 1)) {
          margin-bottom: 40px;
        }

        a {
          font-size: 12px;
          font-weight: 600;
          line-height: 16px;
          color: $color-primary;


          @media only screen and (min-width: ($media-screen-tablet + 1)) {
            font-size: 14px;
            line-height: 18px;
          }
        }

        &:first-child {
          a {
            font-size: 14px;
            line-height: 18px;
            pointer-events: none;
            color: $color-primary-text;

            @media only screen and (min-width: ($media-screen-tablet + 1)) {
              font-size: 16px;
              line-height: 20px;
            }
          }
        }

        &:last-child {
          margin-bottom: 0;
        }
      }
    }
  }
}