@import '../constants/index.scss';

.category {
  gap: 23px;
  display: flex;
  padding-top: 20px;
  padding-bottom: 80px;

  &__sidebar {
    display: none;

    @media only screen and (min-width: ($media-screen-mobile + 1)) {
      display: block;
      min-width: 173px;
      max-width: 173px;
    }

    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      min-width: 219px;
      max-width: 219px;
    }

    &--mobile {
      gap: 8px;
      display: flex;
      margin-bottom: 12px;

      @media only screen and (min-width: ($media-screen-mobile + 1)) {
        display: none;
      }
    }
  }

  &__title {
    font-size: 16px;
    font-weight: 600;
    line-height: 20px;
    margin: 0 0 12px 0;

    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      font-size: 20px;
      font-weight: 700;
      line-height: 28px;
      margin-bottom: 24px;
    }

  }

  &__menu {
    gap: 12px;
    display: flex;
    flex-wrap: wrap;
    padding-bottom: 48px;
    align-items: baseline;
    flex-direction: column;

    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      padding-bottom: 60px;
    }

    &+h3+div {
      padding-bottom: 0
    }

    &--mobile {
      ul {
        margin: 0;
        padding: 0;
        list-style: none;

        li {
          height: 50px;
          display: flex;
          align-items: center;
          justify-content: space-between;
          font-family: "Manrope", sans-serif;
          border-bottom: 1px solid $color-grey-100;

          span {
            display: flex;
            align-items: center;
            font-size: 14px;
            font-weight: 600;
            line-height: 18px;

            svg {
              margin-right: 8px;
            }
          }
        }
      }

      .register-btn {
        width: 100%;
        height: 48px;
        justify-content: center;
      }
    }
  }

  &__result {
    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      margin-right: -70px;
    }
  }

  &__count {
    font-size: 12px;
    font-weight: 400;
    line-height: 20px;
    margin: 0 0 12px 0;

    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      font-size: 16px;
      font-weight: 400;
      line-height: 24px;
      margin-bottom: 24px;
    }
  }

  &__list {
    gap: 12px;
    display: flex;
    overflow: auto;
    flex-wrap: wrap;
    max-height: 684px;

    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      gap: 20px;
      max-height: 1025px;
    }

    &::-webkit-scrollbar {
      width: 0;
    }
  }

  &__item {
    width: 159px;
    height: 159px;
    min-width: 159px;
    position: relative;
    border-radius: $corner-c2;
    border: 1px solid $color-grey-200;

    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      width: 187px;
      height: 187px;
      min-width: 187px;
      border-radius: $corner-c6;
    }

    .tag {
      left: 0;
      gap: 8px;
      right: 0;
      bottom: 0;
      display: flex;
      padding: 12px;
      position: absolute;
      align-items: center;

      span {
        height: 24px;
        font-size: 12px;
        font-weight: 600;
        line-height: 16px;
        padding-left: 8px;
        padding-right: 8px;
        border-radius: 24px;
        align-items: center;
        display: inline-flex;
        vertical-align: middle;
        justify-content: center;
        background-color: $color-grey-100;
      }

      &--online {
        span {
          color: $color-success;
          background-color: $color-success-light;
        }
      }

      &--offline {
        span {
          color: $color-primary;
          background-color: $color-accent;
        }
      }

    }
  }

  .fundiin-btn {
    gap: 2px;
    padding-left: 9px;
    font-weight: 600px;
    padding-right: 9px;

    svg {
      width: 25px;
      height: 25px;
    }
  }

  .filter-btn {
    background-color: $color-grey-100;
  }
}