@import '../constants/index.scss';

.corevalue {
  &__content {
    display: flex;
    flex-direction: column-reverse;

    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      display: grid;
      grid-template-columns: repeat(2, minmax(0, 1fr));
    }
  }

  &__img {
    height: 206px;
    position: relative;

    @media only screen and (min-width: ($media-screen-mobile + 1)) {
      height: 350px;
    }

    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      height: 550px;
    }

    &::after {
      inset: 0;
      content: '';
      display: none;
      position: absolute;

      @media only screen and (min-width: ($media-screen-tablet + 1)) {
        display: block;
        background: linear-gradient(90deg, $color-secondary 0%, rgba(109, 95, 240, 0) 100%);
      }
    }
  }

  &__text {
    display: flex;
    padding: 40px 20px;
    align-items: center;
    color: $color-grey-600;
    background: linear-gradient(180deg, #97FFF8 0%, $color-secondary 100%);

    @media only screen and (min-width: ($media-screen-mobile + 1)) {
      padding: 80px 0;
    }

    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      padding: 0 0 0 40px;
      background: linear-gradient(90deg, #97FFF8 0%, $color-secondary 100%);
    }

    &>div {
      margin: 0 auto;
      max-width: 706px;

      @media only screen and (min-width: ($media-screen-tablet + 1)) {
        margin: 0 40px 0 auto;
        max-width: 560px;
      }
    }

    p {
      font-size: 14px;
      font-weight: 400;
      line-height: 22px;
      text-align: center;

      @media only screen and (min-width: ($media-screen-tablet + 1)) {
        font-size: 16px;
        text-align: left;
        line-height: 24px;
      }
    }
  }

  &__title {
    font-size: 20px;
    font-weight: 700;
    line-height: 28px;
    margin: 0 0 12px 0;
    text-align: center;

    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      font-size: 28px;
      text-align: left;
      line-height: 36px;
    }
  }
}