@import '../constants/index.scss';

.life-bester {
  margin: 0 auto;
  padding-top: 80px;
  text-align: center;
  padding-bottom: 80px;

  @media only screen and (min-width: ($media-screen-tablet + 1)) {
    max-width: 450px;
    padding-top: 155px;
    padding-bottom: 155px;
  }

  &__title {
    font-size: 20px;
    font-weight: 700;
    line-height: 28px;
    margin: 0 0 12px 0;

    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      font-size: 28px;
      line-height: 36px;
    }
  }

  p {
    font-size: 14px;
    font-weight: 400;
    line-height: 22px;
    color: $color-secondary-text;

    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      font-size: 16px;
      line-height: 24px;
    }

    &+p {
      margin-top: 40px;
    }
  }
}