@import '../constants/index.scss';

.register {
  padding-top: 80px;
  position: relative;
  padding-bottom: 30px;
  background: linear-gradient(180deg, $color-primary 0%, $color-secondary 100%);

  @media only screen and (min-width: ($media-screen-tablet + 1)) {
    background: linear-gradient(90deg, $color-primary 0%, $color-secondary 100%);
  }

  .container {
    z-index: 1;
    position: relative;
  }

  &__title {
    font-size: 20px;
    font-weight: 700;
    line-height: 28px;
    margin: 0 0 10px 0;
    color: $color-primary-content;

    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      font-size: 28px;
      font-weight: 700;
      line-height: 36px;
    }
  }

  &__subtitle {
    font-size: 14px;
    font-weight: 400;
    line-height: 22px;
    margin: 0 0 40px 0;
    color: $color-primary-content;

    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      font-size: 16px;
      font-weight: 400;
      line-height: 24px;
    }
  }

  &__form {
    padding: 40px 20px;
    margin-bottom: 40px;
    box-sizing: border-box;
    border-radius: $corner-c2;
    background-color: $color-primary-content;

    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      max-width: 754px;
      padding: 60px 40px;
      border-radius: $corner-c7;
    }

    form {
      @media only screen and (min-width: ($media-screen-tablet + 1)) {
        gap: 0 20px;
        display: grid;
        grid-template-columns: repeat(2, minmax(0, 1fr));
      }
    }

    .ant-form-item-required {
      height: 18px;
      font-size: 14px;
      font-weight: 600;
      line-height: 18px;
      flex-direction: row-reverse;

      &::before {
        margin-inline-end: 0;
        margin-inline-start: 4px;
      }

      &::after {
        display: none;
      }
    }

    .ant-form-item-control-input {
      input {
        border-color: $color-grey-100;
        background-color: $color-grey-100;
      }
    }

    .ant-select-selector {
      border-color: $color-grey-100 !important;
      background-color: $color-grey-100 !important;
    }
  }

  .register-btn {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;

    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      width: 81px;
    }
  }

  &__bg {
    text-align: right;

    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      top: 50%;
      right: 0;
      width: 566px;
      height: 381px;
      position: absolute;
      transform: translate(0, calc(-50% + 80px));
    }
  }

  &__success {
    color: $color-success;
    margin-bottom: 12px;
  }
}

:where(.css-dev-only-do-not-override-1xg9z9n).ant-select-outlined.ant-select-status-error:not(.ant-select-customize-input) .ant-select-selector {
  border: 1px solid #ff4d4f !important;
}

:where(.css-dev-only-do-not-override-1xg9z9n).ant-input-outlined.ant-input-status-error:not(.ant-input-disabled) {
  background-color: $color-grey-100;
}

:where(.css-dev-only-do-not-override-1xg9z9n).ant-modal {
  top: unset;
  padding-bottom: 0;
  overflow-x: hidden;
  margin: 0 auto !important;
}

.ant-modal-wrap {
  display: flex;
  align-items: flex-end;
  overflow: hidden !important;
}