@import 'constants/index.scss';

.container {
  margin: 0 auto;
  max-width: 335px;
  padding-left: 20px;
  padding-right: 20px;

  @media only screen and (min-width: ($media-screen-mobile + 1)) {
    max-width: 706px;
  }

  @media only screen and (min-width: ($media-screen-tablet + 1)) {
    max-width: 1200px;
  }
}

img,
svg {
  max-width: 100%;
  max-height: 100%;
}

a {
  text-decoration: none;
}

button {
  cursor: pointer;
}

p {
  margin: 0;
  letter-spacing: 0;
}

.cover-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.line-clamp {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
  overflow: hidden;
}

.line-clamp2 {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
}

.color-primary {
  color: $color-primary;
}

.color-secondary {
  color: $color-secondary;
}

.color-grey-1000 {
  color: $color-grey-1000
}

.color-grey-600 {
  color: $color-grey-600
}

.color-primary-text {
  color: $color-primary-text
}

.color-secondary-text {
  color: $color-secondary-text
}

.color-primary-content {
  color: $color-primary-content
}

.bg-primary {
  background-color: $color-primary;
}

.bg-secondary {
  background-color: $color-secondary;
}

.bg-grey-1000 {
  background-color: $color-grey-1000
}

.bg-grey-600 {
  background-color: $color-grey-600
}

.bg-primary-text {
  background-color: $color-primary-text
}

.bg-secondary-text {
  background-color: $color-secondary-text
}

.text-center {
  text-align: center;
}

.pointer {
  cursor: pointer;
}

.ant-modal {
  max-height: 100vh;
  overflow-x: hidden;
  max-width: 100vw !important;
  font-family: "Manrope", sans-serif;

  .ant-modal-content {
    min-height: 650px;
    border-radius: 20px 20px 0 0;
    padding: 20px 20px 35px 20px;
  }

  .ant-modal-header {
    margin-bottom: 0;
    padding-top: 12px;
    margin-left: -24px;
    margin-right: -24px;
    padding-bottom: 12px;
    border-bottom: 1px solid $color-grey-100;
  }

  .ant-modal-title {
    margin: 0;
    font-size: 16px;
    font-weight: 600;
    line-height: 20px;
    text-align: center;
    letter-spacing: -0.2px;
    font-family: "Manrope", sans-serif;
  }

  .ant-modal-close {
    width: 32px;
    height: 32px;
    border-radius: 32px;
    background-color: $color-grey-100;
  }

  .ant-modal-body {
    overflow-y: auto;
    overflow-x: hidden;
    height: calc(100% - 80px)
  }

  .ant-modal-footer {
    margin-bottom: 0;

    &__wrap {
      left: 0;
      right: 0;
      bottom: 0;
      height: 5px;
      width: 134px;
      position: fixed;
      margin: 20px auto 10px auto;
      background-color: $color-grey-1000;
    }
  }
}